body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	font-size: 20px;
	background-color: #23313A;
	color: #fff;
	background-image: -webkit-gradient(linear, left top, left bottom, from(#445966), to(#23313A));
	background-image: -webkit-linear-gradient(top, #445966 0%, #23313A 100%);
	background-image: -o-linear-gradient(top, #445966 0%, #23313A 100%);
	background-image: linear-gradient(180deg, #445966 0%, #23313A 100%);
	background-repeat: no-repeat;
	height: 100vh;
	width: 100vw;
	line-height: 1.5;
	text-align: center;
}

ul,
li,
p,
h1,
h2,
h3 {
	margin: 0;
	padding: 0;
	list-style: none;
}



